import React from 'react'
 import GameView from './../components/game/Game.jsx';
  import GameA from 'gamea';

class Game extends React.Component{
    constructor(props){
        super(props);
        console.log("game A constructor");
        console.log(GameA);
        GameA.getSimulation();
    }
    render(){
        console.log("rendering game a");
        return <div>
        <p>Game A</p>
        <GameView />
        </div>
    }
}

export default Game



